.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 3s ease-in-out alternate-reverse;
  height: 1.5rem;
  position: absolute;
  top: 2.45rem;
  right: 2.15rem;
}

.fa-github {
  position: absolute;
  top: 2rem;
  right: 2rem;
  height: 2.5rem;
  color: #61dafb;
}

.App-header {
  background-color: #282c34;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  position: relative;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

button.clickCard {
  width: 12rem;
  height: 12rem;
  margin: 0.5rem 0.5rem;
  padding: 0.2rem;
  background-image: linear-gradient(45deg, lightcoral 25%,#2f6cb7 80%);
  outline: none;

  border: 0.4em #2f6cb7;
  border-style: ridge;
  border-radius: 0.8rem;
}


button.clickCard:hover {
  border-width: 0.2rem;
  border-style: outset;
  border-color: lightcoral;
  
}


button.clickCard:not(.spin) img {
  height: 90%;
  width: 90%;

  object-fit: contain;
  transition: -webkit-transform 0.2s;
  transition: transform 0.2s;
  transition: transform 0.2s, -webkit-transform 0.2s;
}

button.clickCard:hover:not(.spin) img {
  -webkit-filter: 
    drop-shadow(0.5rem 0.5rem rgba(0,0,0,0.5));
          filter: 
    drop-shadow(0.5rem 0.5rem rgba(0,0,0,0.5));

  -webkit-transform: scale(1.21);

          transform: scale(1.21) /* different decimals have better/worse 'blurring' */
}


button.clickCard.spin img {
  position: relative;
  -webkit-animation: spin 0.6s ease-in;
          animation: spin 0.6s ease-in;
}

button.clickCard.danger img {
  -webkit-animation: danger 2s ease-out forwards;
          animation: danger 2s ease-out forwards;
}


@-webkit-keyframes spin { 

  0% {
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 1;
  }

  50% {
    opacity: 0.1;
  } 

  100% {
    bottom: 5rem;
    left: 5rem;
    width: 0%;
    height: 0%;
    opacity: 1;
    -webkit-transform: rotate(1turn);
            transform: rotate(1turn);
    -webkit-transform-origin: 50% 50%;
            transform-origin: 50% 50%;
  } 

}


@keyframes spin { 

  0% {
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 1;
  }

  50% {
    opacity: 0.1;
  } 

  100% {
    bottom: 5rem;
    left: 5rem;
    width: 0%;
    height: 0%;
    opacity: 1;
    -webkit-transform: rotate(1turn);
            transform: rotate(1turn);
    -webkit-transform-origin: 50% 50%;
            transform-origin: 50% 50%;
  } 

}

@-webkit-keyframes danger {
  0% {
    -webkit-filter: invert(0%);
            filter: invert(0%)
  }
  33% {
    -webkit-filter: invert(100%);
            filter: invert(100%)
  }
  67% {
    -webkit-filter: invert(0%);
            filter: invert(0%)
  }
  100% {
    -webkit-filter: invert(100%);
            filter: invert(100%)
  }
}

@keyframes danger {
  0% {
    -webkit-filter: invert(0%);
            filter: invert(0%)
  }
  33% {
    -webkit-filter: invert(100%);
            filter: invert(100%)
  }
  67% {
    -webkit-filter: invert(0%);
            filter: invert(0%)
  }
  100% {
    -webkit-filter: invert(100%);
            filter: invert(100%)
  }
}
#curScore {
  color: lightcoral
}

#bestScore {
  color: #2f6cb7;
}
